export default "import { Box, Stack, Typography } from \"@mui/material\";\nimport {\n  SearchBar,\n  NavBar,\n  CartIcon,\n  MainLogo,\n} from \"../../../../../packages/ui/src\";\n\nexport default function OrderSearch() {\n  return (\n    <Stack\n      direction={\"column\"}\n      justifyContent={\"space-between\"}\n      sx={{ height: \"40rem\" }}\n    >\n      <NavBar cartComponent={<CartIcon />} onSearch={console.log} />\n      <Box sx={{ width: { xs: \"100%\", sm: \"25rem\" }, margin: \"auto\" }}>\n        <Typography variant=\"h3\" sx={{ marginBottom: \"2rem\" }}>\n          Ingresa tu número de pedido\n        </Typography>\n        <SearchBar\n          placeHolder=\"ABC123\"\n          searchButtonContent=\"Buscar\"\n          onSubmit={() => console.log(\"click\")}\n          btnVariant=\"contained\"\n          direction=\"column\"\n        />\n      </Box>\n      <Box\n        justifyContent=\"center\"\n        sx={{ display: { xs: \"none\", sm: \"flex\" }, width: \"100%\" }}\n      >\n        <MainLogo />\n      </Box>\n    </Stack>\n  );\n}\n"